body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.machine {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 4px;
  border: 1px solid rgb(0, 0, 0);
}

.machines {
  background-color: rgb(211, 241, 241);
  width: 192px;
  height: 270px;
  border: 1px solid rgb(147, 187, 187);
  border-radius: 4px;
}

.selected {
  width: 50px;
  height: 50px;
  display: inline-block;
  margin: 4px;
  border: 1px solid rgb(240, 232, 117);
}
.dpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.lpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.upiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.rpiece {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(0, 0, 0);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.dselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(0deg);
  transform: rotate(0deg);
}

.lselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(90deg);
  transform: rotate(90deg);
}

.uselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.rselected {
  width: 55px;
  height: 55px;
  display: inline-table;
  margin: 0px;
  margin-top: -7px;
  border: 1px solid rgb(240, 232, 117);
  -webkit-transform: rotate(270deg);
  transform: rotate(270deg);
}

.dselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.lselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.uselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.rselected:hover {
  border: 1px solid rgb(255, 255, 255);
}

.dpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.lpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.upiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.rpiece:hover {
  border: 1px solid rgb(255, 255, 255);
}

.panel {
  width: 575px;
  height: 575px;
  display: inline-block;
}

.details {
    background-color: rgb(211, 241, 241);
    width: 592px;
    height: 210px;
    border: 1px solid rgb(147, 187, 187);
    border-radius: 4px;
  }
.earnings {
  background-color: rgb(211, 241, 241);
  width: 292px;
  height: 30px;
  border: 1px solid rgb(147, 187, 187);
  border-radius: 4px;
}
.App {
  text-align: center;
}
